import React, { Component } from "react";
import Navbar from "./components/Navbar";
import FloatButtons from "./components/FloatButtons";
import Modal from "./components/Modal";
import { withTranslation } from 'react-i18next';

let deferredPrompt;

class App extends Component {
  constructor() {
    super();

    this.state = {
      openCopyModal: false,
      openRightButton: false,
      openInstallModal: false
    };
  }

  handleCopyModal = () => {
    this.handleErrorModal(false);
  };

  handleErrorModal = (value) => {
    this.setState({ openCopyModal: value });
  }

  handleCopy = event => {
    event.preventDefault();
    this.handleErrorModal(true);
  };

  handleInstall = event => {
    event.preventDefault();
    this.handleInstallModal(true);
  };

  handleInstallModal = (value) => {
    this.setState({ openInstallModal: value });
  }

  handleBeforeInstallEvent = () => {
    window.addEventListener('beforeinstallprompt', (e) => {
      console.log("INSTALABLE");
      // Prevent the mini-infobar from appearing on mobile
      e.preventDefault();
      // Stash the event so it can be triggered later.
      deferredPrompt = e;
      // Update UI notify the user they can install the PWA
      // this.handleInstallModal(true);
      // Show the prompt
      deferredPrompt.prompt();
      // Wait for the user to respond to the prompt
      deferredPrompt.userChoice
        .then((choiceResult) => {
          if (choiceResult.outcome === 'accepted') {
            console.log('User accepted the A2HS prompt');
          } else {
            console.log('User dismissed the A2HS prompt');
          }
          deferredPrompt = null;
        });
    });
  }

  componentDidMount() {
    this.handleBeforeInstallEvent();
  }

  render() {
    const { openCopyModal, openInstallModal } = this.state;

    return (
      <div
        className="App"
        onCopy={this.handleCopy}
        onContextMenu={this.handleRightClick}
      >
        <Modal
          title={this.props.t('modal.copy.title').toUpperCase()}
          text={this.props.t('modal.copy.content')}
          open={openCopyModal}
          handleClose={this.handleCopyModal}
        />

        <Modal
          title={"¿DESEA INSTALAR?"}
          text={"Se añadirá como una nueva app"}
          open={openInstallModal}
          handleClose={this.handleInstall}
        />

        <Navbar />
        <FloatButtons />
      </div>
    );
  }
}

export default withTranslation('common')(App);
